import React,{Fragment} from 'react';
import Scrollbar from 'perfect-scrollbar-react';
import 'perfect-scrollbar-react/dist/style.min.css';
import Investment from "../../components/investment";
import Loading from "../../components/loading";

const InvestmentThreeColumn = ({Investments,loading}) => {
 
    const isMobile = window.innerWidth < 992;

    return (
        <Fragment>
            <div className="content-body section">
                <div style={{display: 'flex', maxHeight: isMobile ? 'auto' : 'calc(100vh )'}}>
                    <Scrollbar options={{suppressScrollX: true}} enable={!isMobile}>
                        <div className="section-wrap section">
                            <div className="row row-7 investment-column-three">
                                {Investments.map(investment => (
                                    <div key={investment.id} className="col-lg-4 col-md-6 col-12 mb-15">
                                        <Investment
                                            id={investment.id}
                                            title={investment.title}
                                            thumb={investment.thumb}
                                            category={investment.category}
                                            variant={'column'}
                                        />
                                    </div>
                                ))}
                            </div>
                        </div>
                    </Scrollbar>
                </div>
            </div>

            {loading ? <Loading/> : null}
        </Fragment>
    );
};

export default InvestmentThreeColumn;
