import React, {Fragment, useMemo} from 'react';
import {useLocation} from "react-router-dom";
import Validators from "../../../data/validators";
import SideHeader from "../../../components/SideHeader";
import Header from "../../../components/header/HeaderOne";
import FooterThree from "../../../components/footer/FooterThree";
import TemplateValidatingDetails from "../../../templates/validating/details";


function useQuery() {
    const {search} = useLocation();

    return useMemo(() => new URLSearchParams(search), [search]);
}

const ValidatingDetailsPage = () => {
    const query = useQuery()
    const id = query.get("id");
    const ValidatorsParsed = Validators?.find(Validating => Validating.id === parseInt(id, 10));

    return (
        <Fragment>
            <Header classes={'position-static'}/>
            <SideHeader mobile={true}/>
            <TemplateValidatingDetails Validator={ValidatorsParsed}/>
            <FooterThree position='fixed'/>
        </Fragment>
    );
};

export default ValidatingDetailsPage;