import React, {useState} from 'react';

// File imported
import Header from '../../components/header/HeaderOne'
import SideHeader from "../../components/SideHeader";
import ModeratorThreeColumn from "../../container/moderator/ModeratorThreeColumn";
import FooterThree from "../../components/footer/FooterThree";
import ModeratorData from '../../data/moderators'

const Moderators = () => {
    const [count, setCount] = useState(9);
    const [loading, setLoading] = useState(false);
    const Moderators = ModeratorData.slice(0, count);

    const loadMore = () => {
        setLoading(true);
        setTimeout(() => {
            setCount(count + 3);
            setLoading(false);
        }, 100);
    };

    const allItems = count === ModeratorData.length;

    return (
        <div className={'main-wrapper'}>
            <Header/>
            <SideHeader mobile={true}/>
            <ModeratorThreeColumn Moderators={Moderators} loading={loading}/>
            <FooterThree loadHandler={loadMore} allItems={allItems}/>
        </div>
    );
};

export default Moderators;
