import React, {useState} from 'react';

// File imported
import Header from '../../components/header/HeaderOne'
import SideHeader from "../../components/SideHeader";
import ValidatorThreeColumn from "../../container/Validator/ValidatorThreeColumn";
import FooterThree from "../../components/footer/FooterThree";
import ValidatorData from '../../data/validators'

const Validators = () => {
    const [count, setCount] = useState(9);
    const [loading, setLoading] = useState(false);
    const Validators = ValidatorData.slice(0, count);

    const loadMore = () => {
        setLoading(true);
        setTimeout(() => {
            setCount(count + 3);
            setLoading(false);
        }, 100);
    };

    const allItems = count === ValidatorData.length;

    return (
        <div className={'main-wrapper'}>
            <Header/>
            <SideHeader mobile={true}/>
            <ValidatorThreeColumn Validators={Validators} loading={loading}/>
            <FooterThree loadHandler={loadMore} allItems={allItems}/>
        </div>
    );
};

export default Validators;
