import React, {Fragment, useMemo} from 'react';
import {useLocation} from "react-router-dom";
import Investments from "../../../data/investments";
import SideHeader from "../../../components/SideHeader";
import Header from "../../../components/header/HeaderOne";
import FooterThree from "../../../components/footer/FooterThree";
import TemplateInvestmentDetails from "../../../templates/investment/details";


function useQuery() {
    const {search} = useLocation();

    return useMemo(() => new URLSearchParams(search), [search]);
}

const InvestmentDetailsPage = () => {
    const query = useQuery()
    const id = query.get("id");
    const Investment = Investments?.find(Investment => Investment.id === parseInt(id, 10));

    return (
        <Fragment>
            <Header classes={'position-static'}/>
            <SideHeader mobile={true}/>
            <TemplateInvestmentDetails Investment={Investment}/>
            <FooterThree position='fixed'/>
        </Fragment>
    );
};

export default InvestmentDetailsPage;